.dashboard-automation {
  position: relative;
  padding: 1.5rem;
  background-color: #f9fafb;
  min-height: 100vh;
}
.dashboard-automation .section-header {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-bottom: 0.5rem;
}
.dashboard-automation .section-header svg {
  color: #6b7280;
}
.dashboard-automation .section-header .section-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #111827;
  margin: 0;
}
.dashboard-automation .section-subtitle {
  color: #6b7280;
  font-size: 1rem;
  margin: 0 0 2rem 0;
}
.dashboard-automation .smart-reorder-section {
  margin-bottom: 3rem;
}
.dashboard-automation .smart-reorder-section .automation-settings-card {
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}
.dashboard-automation .smart-reorder-section .automation-settings-card .card-header {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-bottom: 0.5rem;
}
.dashboard-automation .smart-reorder-section .automation-settings-card .card-header svg {
  color: #6b7280;
}
.dashboard-automation .smart-reorder-section .automation-settings-card .card-header .card-title {
  font-size: 1.125rem;
  font-weight: 600;
  color: #111827;
  margin: 0;
}
.dashboard-automation .smart-reorder-section .automation-settings-card .card-description {
  color: #6b7280;
  font-size: 0.875rem;
  margin: 0 0 1.5rem 0;
}
.dashboard-automation .smart-reorder-section .automation-settings-card .settings-options .setting-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0;
  border-bottom: 1px solid #f3f4f6;
}
.dashboard-automation .smart-reorder-section .automation-settings-card .settings-options .setting-item:last-child {
  border-bottom: none;
}
.dashboard-automation .smart-reorder-section .automation-settings-card .settings-options .setting-item .setting-info {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}
.dashboard-automation .smart-reorder-section .automation-settings-card .settings-options .setting-item .setting-info .setting-label {
  font-size: 0.875rem;
  font-weight: 500;
  color: #111827;
}
.dashboard-automation .smart-reorder-section .automation-settings-card .settings-options .setting-item .setting-info .setting-note {
  font-size: 0.75rem;
  color: #6b7280;
}
.dashboard-automation .smart-reorder-section .automation-settings-card .settings-options .setting-item .toggle-switch {
  position: relative;
  width: 3rem;
  height: 1.5rem;
  background-color: #d1d5db;
  border-radius: 9999px;
  cursor: pointer;
  transition: background-color 0.2s;
}
.dashboard-automation .smart-reorder-section .automation-settings-card .settings-options .setting-item .toggle-switch.on {
  background-color: #3b82f6;
}
.dashboard-automation .smart-reorder-section .automation-settings-card .settings-options .setting-item .toggle-switch .toggle-slider {
  position: absolute;
  top: 0.125rem;
  left: 0.125rem;
  width: 1.25rem;
  height: 1.25rem;
  background-color: white;
  border-radius: 50%;
  transition: transform 0.2s;
}
.dashboard-automation .smart-reorder-section .automation-settings-card .settings-options .setting-item .toggle-switch.on .toggle-slider {
  transform: translateX(1.5rem);
}
.dashboard-automation .reorder-alerts-section .alert-item {
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.dashboard-automation .reorder-alerts-section .alert-item .alert-left {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.dashboard-automation .reorder-alerts-section .alert-item .alert-left .severity-badge {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 0.75rem;
  border-radius: 0.375rem;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  color: white;
}
.dashboard-automation .reorder-alerts-section .alert-item .alert-left .severity-badge.high {
  background-color: #ef4444;
}
.dashboard-automation .reorder-alerts-section .alert-item .alert-left .severity-badge svg {
  color: white;
}
.dashboard-automation .reorder-alerts-section .alert-item .alert-left .alert-content .part-name {
  display: block;
  font-size: 1rem;
  font-weight: 600;
  color: #111827;
  margin-bottom: 0.5rem;
}
.dashboard-automation .reorder-alerts-section .alert-item .alert-left .alert-content .stock-details {
  display: flex;
  gap: 1.5rem;
}
.dashboard-automation .reorder-alerts-section .alert-item .alert-left .alert-content .stock-details .stock-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.875rem;
  color: #6b7280;
}
.dashboard-automation .reorder-alerts-section .alert-item .alert-left .alert-content .stock-details .stock-item svg {
  color: #9ca3af;
}
.dashboard-automation .reorder-alerts-section .alert-item .alert-right {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 0.75rem;
}
.dashboard-automation .reorder-alerts-section .alert-item .alert-right .pricing-status {
  text-align: right;
}
.dashboard-automation .reorder-alerts-section .alert-item .alert-right .pricing-status .price {
  display: block;
  font-size: 1.125rem;
  font-weight: 600;
  color: #111827;
}
.dashboard-automation .reorder-alerts-section .alert-item .alert-right .pricing-status .status {
  display: block;
  font-size: 0.875rem;
  color: #ef4444;
}
.dashboard-automation .reorder-alerts-section .alert-item .alert-right .create-po-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  background-color: #3b82f6;
  color: white;
  border: none;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}
.dashboard-automation .reorder-alerts-section .alert-item .alert-right .create-po-btn:hover {
  background-color: #2563eb;
}
.dashboard-automation .reorder-alerts-section .alert-item .alert-right .create-po-btn svg {
  color: white;
}
